import React, { useState } from "react";

const BookForm = ({ createBook }) => {
  const [newBookTitle, setNewBookTitle] = useState("");
  const [newBookAuthor, setNewBookAuthor] = useState("");
  const [newBookUrl, setNewBookUrl] = useState("");
  const [newBookStars, setNewBookStars] = useState("");
  const [newBookPicture, setNewBookPicture] = useState("");

  // CHANGE HANDLERS
  const handleTitleChange = (event) => setNewBookTitle(event.target.value);
  const handleAuthorChange = (event) => setNewBookAuthor(event.target.value);
  const handleUrlChange = (event) => setNewBookUrl(event.target.value);
  const handleStarsChange = (event) => setNewBookStars(event.target.value);
  const handlePictureChange = (event) => setNewBookPicture(event.target.value);

  // CREATING BLOG OBJECT
  const addBook = (event) => {
    event.preventDefault();
    createBook({
      title: newBookTitle,
      author: newBookAuthor,
      url: newBookUrl,
      stars: newBookStars,
      picture: newBookPicture,
    });

    setNewBookTitle("");
    setNewBookAuthor("");
    setNewBookUrl("");
    setNewBookStars("");
    setNewBookPicture("");
  };

  return (
    <div className="formDiv">
      <h2>Add a new book</h2>
      <form onSubmit={addBook}>
        <p>
          Title:{" "}
          <input
            id="titleInput"
            value={newBookTitle}
            onChange={handleTitleChange}
            className="titleInput"
          />
        </p>
        <p>
          Author:{" "}
          <input
            id="authorInput"
            value={newBookAuthor}
            onChange={handleAuthorChange}
            className="authorInput"
          />
        </p>
        <p>
          Url:{" "}
          <input
            id="urlInput"
            value={newBookUrl}
            onChange={handleUrlChange}
            className="urlInput"
          />
        </p>
        <p>
          Stars:{" "}
          <input
            id="starsInput"
            value={newBookStars}
            onChange={handleStarsChange}
            className="starsInput"
          />
        </p>
        <p>
          Picture:{" "}
          <input
            id="pictureInput"
            value={newBookPicture}
            onChange={handlePictureChange}
            className="pictureInput"
          />
        </p>
        <button id="newBookButton" type="submit">
          Add
        </button>
      </form>
    </div>
  );
};

export default BookForm;
